import { Component, OnInit, Input } from '@angular/core';

/**
 * Item del menu
 *
 * @export
 * @class BcMenuItemComponent
 * @implements {OnInit}
 */
@Component({
  selector: 'bc-menu-item',
  templateUrl: './bc-menu-item.component.html',
})
export class BcMenuItemComponent implements OnInit {
  /**
   * Texto del item
   *
   * @type {string}
   * @memberof BcMenuItemComponent
   */
  @Input() text: string;

  /**
   * item activo por defecto
   *
   * @type {string}
   * @memberof BcMenuItemComponent
   */
  @Input() itemActive = 'false';
  /**
   * Nombre del icono
   *
   * @type {string}
   * @memberof BcMenuItemComponent
   */
  @Input() iconName: string;
  /**
   * Ruta or url del item
   *
   * @type {string}
   * @memberof BcMenuItemComponent
   */
  @Input() path?: string;
  /**
   * Id del elemento
   *
   * @type {string}
   * @memberof BcMenuItemComponent
   */
  @Input() identifierContent?: string;

  ngOnInit() {}
}
