import { Component, Input, ViewChild, ElementRef, AfterViewInit, ChangeDetectionStrategy, OnInit } from '@angular/core';
import { BcMenuBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

export interface BcMenuProfileData {
  user: string;
  updateUserDataUrl?: string;
}

/**
 * Componente menú
 *
 * @export
 * @class BcMenuComponent
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-menu',
  templateUrl: './bc-menu.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BcMenuComponent implements AfterViewInit, OnInit {
  /**
   * Configuración del menú
   *
   * @memberof BcMenuComponent
   */
  @Input() config;
  /**
   * Estado inicial del menu, determina si inicia plegado o desplegado
   *
   * @memberof BcMenuComponent
   */
    @Input() state: 'open' | 'close' = 'open';
  /**
   * Id del elemento
   *
   * @memberof BcMenuComponent
   */
  @Input() componentId = this.bcHelperService.getId(this);
  /**
   * Evento para el botón de cerrar sesión
   *
   * @memberof BcMenuComponent
   */
  @Input() logoutFunction: (param?: any) => void;
  /**
   * Permite ocultar el perfil del usuario en el submenu cuando está en mobile.
   *
   * @memberof BcMenuComponent
   */
  @Input() hideProfileInSubmenu = false;
  /**
   * Permite setear el nombre del usuario y la url para la actualización de datos (opcional)
   *
   * @memberof BcMenuComponent
   */
  @Input() profileData: BcMenuProfileData = { user: '', updateUserDataUrl: '' };

  @ViewChild('byConfig', { static: true }) byConfig: ElementRef;

  bcMenuBehavior: BcMenuBehavior;

  constructor(private bcHelperService: BcHelperService) {}

  ngOnInit(): void {}

  ngAfterViewInit(): void {
    this.bcMenuBehavior = new BcMenuBehavior();
    if (this.config != undefined) {
      this.generateMenu();
    } else {
      this.bcMenuBehavior.setId(this.componentId);
      this.bcMenuBehavior.setLogoutEvent(this.logoutFunction);
      this.setProfileData();
      this.bcMenuBehavior.setUp();
    }
  }

  /**
   * Genera el menú apartir de la configuración
   *
   * @memberof BcMenuComponent
   */
  generateMenu() {
    this.bcMenuBehavior.generateByConfig(this.config, this.byConfig.nativeElement);
  }

  setProfileData(): void {
    if (this.profileData.user !== '') {
      this.bcMenuBehavior.profileData = this.profileData;
    }
  }
}
